  watch: {
    countryCodeSelected(val, old) {
      let self = this;
      document.getElementsByClassName(
        "tk-select-border-none"
      )[0].childNodes[1].childNodes[1].style.display = "none";
      self.countrySelects.options.some((item) => {
        if (item.value === val) {
          setTimeout(() => {
            document.getElementsByClassName(
              "tk-select-border-none"
            )[0].childNodes[1].childNodes[1].value = item.areaCode;
            document.getElementsByClassName(
              "tk-select-border-none"
            )[0].childNodes[1].childNodes[1].style.display = "block";
          }, 10);
        }
      });
    },
  },
  mounted() {
    let self = this;
    setTimeout(function () {
      document.getElementById("uploadLogo").childNodes[1].innerText =
        "Drop logo here to upload";
      document.getElementById("uploadLogo").childNodes[1].style.height =
        "210px";
      document.getElementById("uploadLogo").childNodes[1].style.lineHeight =
        "56px";
      document.getElementById("storefrontColor").style.marginTop = "16px";
    }, 200);
    // console.log(document.getElementsByClassName("tk-select-border-none")[0].childNodes[1].childNodes);
  },
  methods: {
    previewStudio() {
      this.preview = true;
    },
    collapsePreview() {
      this.preview = false;
    },
    validationEmail() {
      let self = this;
      if (self.$tools.isNull(self.email)) {
        self.isInvalid = false;
      } else {
        self.isInvalid = !self.$tools.isEmail(self.email);
      }
    },
  },
};
